import { Form, useActionData } from "@remix-run/react";
import { json, redirect } from "@remix-run/node";
import { getSession } from "~/sessions";
import connectDb from "~/db/connectDb.server";

// check if session is valid - if not, redirect to login
export async function loader({ request }) {
  const session = await getSession(request.headers.get("Cookie"));
  //if (!session.get("userId")) {
  // return redirect("/login");
  //}
  return json({
    userId: session.get("userId"),
  });
}

export async function action({ request }) {
  const form = await request.formData();
  const db = await connectDb();
  try {
    const newPost = await db.models.Post.create({
      title: form.get("title"),
      language: form.get("language"),
      post: form.get("post"),
      description: form.get("description"),
      image: form.get("image"),
    });
    return redirect(`/explore/${newPost._id}`);
  } catch (error) {
    return json(
      { errors: error.errors, values: Object.fromEntries(form) },
      { status: 400 }
    );
  }
}

export default function CreatePost() {
  const actionData = useActionData();
  return (
    <div>
      <h1 className="text-xl">Create code post</h1>
      <Form method="post">
        <label htmlFor="title" className="block">
          Title
        </label>
        <input
          type="text"
          name="title"
          defaultValue={actionData?.values.title}
          id="title"
          className={
            actionData?.errors.title ? "border-2 border-red-500" : null
          }
        />

        <label htmlFor="post" className="block">
          Tekst
        </label>
        <textarea
          type="text"
          maxLength={500}
          name="post"
          defaultValue={actionData?.values.post}
          id="post"
          className={
            actionData?.errors.post ? "border-2 border-red-500" : null
          }
        />

        <label htmlFor="image" className="block">
          Image
        </label>
        <input type="file" name="image" id="image" />

        {actionData?.errors.title && (
          <p className="text-red-500">{actionData.errors.title.message}</p>
        )}
        <br />
        <button
          className="shadow-lg bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 rounded mt-10"
          type="submit"
        >
          Create new post
        </button>
      </Form>
    </div>
  );
}
